"use client";

import { useState } from "react";
import { useHabits } from "@/hooks/useHabits";
import { Plus, Flame, Trash2, Check } from "lucide-react";
import clsx from "clsx";

export default function HabitsPage() {
  const { habits, loading, addHabit, deleteHabit } = useHabits();
  const [newHabit, setNewHabit] = useState("");
  const [difficulty, setDifficulty] = useState<"easy" | "medium" | "hard">("medium");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newHabit.trim()) return;
    await addHabit(newHabit, difficulty);
    setNewHabit("");
    setDifficulty("medium");
  };

  if (loading) {
    return <div className="p-8 text-[var(--color-text)]">Loading habits...</div>;
  }

  return (
    <div className="mx-auto max-w-4xl">
      <h1 className="mb-8 text-4xl font-bold" style={{ color: "var(--color-text)" }}>Habit Builder</h1>

      <div className="mb-8">
        <form onSubmit={handleSubmit} className="cozy-card flex flex-col gap-4 p-4 sm:flex-row sm:items-center">
          <div className="flex flex-1 items-center">
            <Flame className="mr-3 h-6 w-6 text-[var(--color-danger)]" />
            <input
              type="text"
              value={newHabit}
              onChange={(e) => setNewHabit(e.target.value)}
              placeholder="New habit to build..."
              className="flex-1 bg-transparent text-lg font-medium placeholder-opacity-50 outline-none"
              style={{ color: "var(--color-text)" }}
            />
          </div>
          
          <div className="flex items-center gap-2">
            <select
              value={difficulty}
              onChange={(e) => setDifficulty(e.target.value as "easy" | "medium" | "hard")}
              className="rounded-[var(--border-radius)] border-2 border-[var(--color-text)] bg-transparent px-3 py-2 font-bold outline-none focus:border-[var(--color-primary)]"
              style={{ color: "var(--color-text)" }}
            >
              <option value="easy">Easy</option>
              <option value="medium">Medium</option>
              <option value="hard">Hard</option>
            </select>
            
            {newHabit && (
              <button
                type="submit"
                className="cozy-btn bg-[var(--color-primary)] px-6 py-2 text-white hover:bg-[var(--color-secondary)]"
              >
                ADD
              </button>
            )}
          </div>
        </form>
      </div>

      <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
        {habits.map((habit) => {
          const today = new Date();
          today.setHours(0, 0, 0, 0);
          const todayTimestamp = today.getTime();
          const isCompletedToday = habit.completedDates.includes(todayTimestamp);

          return (
            <div
              key={habit.id}
              className={clsx(
                "cozy-card flex flex-col justify-between p-6 transition-all hover:-translate-y-1",
                isCompletedToday && "bg-[var(--color-bg)]"
              )}
            >
              <div>
                <div className="mb-2 flex items-start justify-between">
                  <div>
                    <h3 className="text-xl font-bold" style={{ color: "var(--color-text)" }}>{habit.name}</h3>
                    <span className={clsx(
                      "mt-1 inline-block rounded-full px-2 py-0.5 text-xs font-bold uppercase tracking-wider text-white",
                      habit.difficulty === "easy" && "bg-green-500",
                      habit.difficulty === "medium" && "bg-orange-500",
                      habit.difficulty === "hard" && "bg-red-500",
                      !habit.difficulty && "bg-orange-500" // Fallback
                    )}>
                      {habit.difficulty || "medium"}
                    </span>
                  </div>
                  <button
                    onClick={() => deleteHabit(habit.id)}
                    className="text-[var(--color-text)] opacity-0 transition-opacity hover:text-[var(--color-danger)] group-hover:opacity-100"
                  >
                    <Trash2 className="h-4 w-4" />
                  </button>
                </div>
                <div className="mt-2 flex items-center gap-2 text-[var(--color-secondary)]">
                  <Flame className="h-5 w-5 fill-current" />
                  <span className="font-bold">{habit.streak} day streak</span>
                </div>
              </div>

              <div
                className={clsx(
                  "mt-4 flex w-full items-center justify-center rounded-[var(--border-radius)] py-2 font-bold transition-all",
                  isCompletedToday
                    ? "bg-[var(--color-secondary)] text-white"
                    : "bg-[var(--color-bg)] text-[var(--color-text)] opacity-70"
                )}
              >
                {isCompletedToday ? (
                  <>
                    <Check className="mr-2 h-5 w-5" />
                    Done!
                  </>
                ) : (
                  <span className="text-sm">Manage in To-Do List</span>
                )}
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
